<script>
import {mixin} from '../mixins/allDocAndPat'
import { parseTime2String } from '../tools/utils'
import {createDoc,updateDoc,doctorintoGroup,getDocInProjectList,getReviewList,updateStatus} from '../service/getData'
import validator from 'validator';
export default {
  mixins: [mixin],
  data() {
    var validatePhone = (rule, value, callback) => {
          if (!validator.isMobilePhone(value,'zh-CN')) {
            callback(new Error(''));
          }  else {
            callback();
          }
      };
    return {
      groupBy: "",
      searchText: "", // 
      projectInfo:'',//
      tableData: [
      ],
      formLabelWidth: "80px",
      addDocDialog: false, // 
      addNewDocDialog: false,// 
      aduitDocDialog: false, // 
      editDocInfoTableDialog: false, // 
      addDoc: {
        joinDoctorId:'',// id
      },
      addDocForm : {
        // 
        // "name": "",
        // "username": "",
        // "hospital": "",
        // "gender": 0,
        // "department": "",
        // "title": "",
        // "phoneNumber": ""

      },
      editDocInfoTable: {
        // 
        
      },
      willAuditList: [
        // 
        
      ],
      hasAuditList: [
        // 
        
      ],
      disAuditList: [
        // 
        ],
      validatePhoneRules:[
      { required: true, message: '', trigger: 'blur' },
      { validator: validatePhone, trigger: 'blur'}
    ]
    };
  },
  methods: {
    // 
    submitNewDoc(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          const params = {
            doctorId : this.addDoc.joinDoctorId,
            projectId : this.projectInfo.id
          }
         this.handleDoctorintoGroup(params);
        } else {
          return false;
        }
      });
    },
    // 
    submitCreate(ref){
      this.$refs[ref].validate(valid => {
        if (valid) {
          this.handleCreateDoc(this.addDocForm);
        } else {
          return false;
        }
      });
    },
    // 
    submitDocInfoTable(ref) {
      this.$refs[ref].validate(valid => {
        if (valid) {
          let {id,name,username,hospital,gender,department,title,phoneNumber} = this.editDocInfoTable;
          let params = {
          id,name,username,hospital,gender,department,title,phoneNumber
        }
        this.handleUpdateDoc(params);
        } else {
          return false;
        }
      });
    },
    // 
    async handleCreateDoc(params){
      let res = await createDoc(params);
      let {
        code,
        data,
        message
      } = res.data;
      if(code === 200){
         this.$message({
          message: "",
          type: "success"
        });
        let {id,name} = data;
        this.addDoc.joinDoctorId = id;
        this.addNewDocDialog = false;
        this.addDocForm =  {}
        await this.handleGetDoc();
      } else {
        this.$message.error(message);
      }
    },
    // 
    async handleDoctorintoGroup(params){
      let res = await doctorintoGroup(params);
      let {code,message} = res.data;
      if(code == 200){
        this.handleGetList({projectId : params.projectId})
        this.addDocDialog = false;
        this.addDoc.joinDoctorId = '';
      } else {
        this.$message.error(message);
      }
    },
    // 
    edit(row) {
      // console.log(row);
      this.editDocInfoTable = row;
      this.editDocInfoTableDialog = true;
    },
    // 
    async handleUpdateDoc(params){
      let res = await updateDoc(params);
      let { code, message } = res.data;
      if (code === 200) {
        this.$message({
          message: "",
          type: "success"
        });
        this.handleGetList({projectId : this.projectInfo.id})
        this.editDocInfoTableDialog = false;
        this.editDocInfoTable = {};
      } else {
        this.$message.error(message);
      }

    },
    // 
    async handleGetList(query){
      let res = await getDocInProjectList(query);
      let { code, message ,data} = res.data;
      if (code === 200) {
        this.tableData = data;
      } else {
        this.$message.error(message);
      }
    },
    // 
    searchDoc(){
        if(this.searchText.trim() == ''){
          this.handleGetList({projectId : this.projectInfo.id});
          return;
        }
        let result = this.tableData.filter(item => {
          return item.name.includes(this.searchText)
        })
        this.tableData = result;

    },
    // 
    async handleGetReviewList(params){
      let res = await getReviewList(params);
      let {code , message,data} = res.data;
      if (code === 200) {
        let {status} = params;
        switch(status){
          case 4 :{ // 
            this.disAuditList = data;
            break
          }
          case 3 :{ // 
            this.hasAuditList = data;
            break
          }
          case 2 :{ // 
            this.willAuditList = data;
            break
          }
      }
      } else {
        this.$message.error(message);
      }
    },
    // ()
    async handleUpdateStatus(query){
      let res = await updateStatus(query);
      let {code , message,data} = res.data;
      if(code == 200){
        this.handleGetReviewList({projectId : this.projectInfo.id,status:2});// 
        this.handleGetReviewList({projectId : this.projectInfo.id,status:3});// 
        this.handleGetReviewList({projectId : this.projectInfo.id,status:4}); // 
        this.handleGetList({projectId : this.projectInfo.id});
      } else {
        this.$message.error(message);
      }
    },
    // 
    agree(row){
      let { doctorId } = row;
      let query = {
        "doctorId": doctorId,
        "projectId":this.projectInfo.id,
        "status": 3
      }
      this.handleUpdateStatus(query);
      this.handleGetList({projectId : this.projectInfo.id});

    },
    // 
    refuse(row){
      let { doctorId } = row;
      let query = {
        "doctorId": doctorId,
        "projectId":this.projectInfo.id,
        "status": 4
      }
      this.handleUpdateStatus(query);
    }
  },
  filters:{
    parseTime(timestamp){
     return parseTime2String(parseInt(timestamp))
    },
    showStatus(status){
      switch(status){
        case 1 :{
          return ''
          break
        }
        case 2 :{
          return ''
          break
        }
        case 3 :{
          return ''
          break
        }
      }

    },
  },
  created() {
    // console.log(this.$store.state.projectInfo);
    const projectInfo = this.$store.state.projectInfo;
    this.projectInfo = projectInfo;
    this.handleGetList({projectId : projectInfo.id});
    this.handleGetReviewList({projectId : this.projectInfo.id,status:2});// 
    this.handleGetReviewList({projectId : this.projectInfo.id,status:3});// 
    this.handleGetReviewList({projectId : this.projectInfo.id,status:4}); // 
  },
};
</script>
