        <el-table-column prop="createTime" label="" width="120" align="center">
          <template slot-scope="scope">
            {{scope.row.createTime | parseTime}}
          </template>
        </el-table-column>
        <el-table-column prop="questionSize" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="doctorSize" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="recruitedPatients" label="" width="80" align="center"></el-table-column>
        <el-table-column prop="status" label="" width="80" align="center">
          <template slot-scope="scope">
            <span :style="{color:statusColor[scope.row.status]}">
              {{scope.row.status | showStatus}}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="" width="200" align="center">
          <template slot-scope="scope" class="btns">
            <el-button icon="el-icon-edit" @click.stop="edit(scope.row)" size="mini"></el-button>
            <el-button icon="el-icon-delete" size="mini" @click.stop="delRow(scope.row)"></el-button>
            <a-icon type="right-circle" @click="rowClick(scope.row)" style="font-size:20px;color:#72B4DE;margin-left: 10px;vertical-align: middle;cursor:pointer;"/>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
         background
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pagesize"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pages"
      ></el-pagination>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addProjectDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
      >
        <el-form :model="addProform" ref="addProform">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="type"
            :rules="{ required: true, message: '', trigger: 'change' }"
          >
            <el-select v-model="addProform.type" placeholder="">
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="name"
            :rules="[{ required: true, message: '', trigger: 'blur' }]"
          >
            <el-input v-model="addProform.name" autocomplete="off" placeholder=''></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input type='textarea' v-model="addProform.description" autocomplete="off"  placeholder=""></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="details"
            :rules="[{ required: true, message: '', trigger: 'blur' }]"
          >
            <el-input type='textarea' v-model="addProform.details" autocomplete="off"  placeholder=""></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            :rules="[{ required: true}]"
          >
            <el-col :span="11">
                <el-form-item prop="group1Name" :rules="[{ required: true, message: '',trigger: 'blur' }]">
                    <el-input v-model="addProform.group1Name" autocomplete="off" class="groupNameInput" placeholder=''></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="10">
                <el-form-item  prop="group1Count" :rules="[{ required: true, message: '',trigger: 'blur' }]">
                    <label>:</label><el-input v-model="addProform.group1Count" autocomplete="off" class="groupCountInput" type='number' placeholder=''></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <i class="el-icon-circle-plus-outline" @click="increament"></i>
                <!-- <i class="el-icon-remove-outline"></i> -->
            </el-col>
          </el-form-item>
          <!-- item -->
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            v-for="(item, index) in addProform.groupArr" :key="index"
          >
            <el-col :span="11">
                <el-form-item prop="group1Name">
                    <el-input v-model="item.name" autocomplete="off" class="groupNameInput" placeholder=''></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="10">
                <el-form-item  prop="group1Count">
                    <label>:</label><el-input v-model="item.sampleSize" autocomplete="off" class="groupCountInput" type='number' placeholder=''></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <i class="el-icon-circle-plus-outline" @click="increament"></i>
                <i class="el-icon-remove-outline" @click="decrement(index)"></i>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitCreate('addProform')"> </el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editProjectDialog"
        class="addPlatform"
        width="760px"
        :close-on-click-modal="false"
      >
        <el-form :model="editform" ref="editform">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="type"
            :rules="{ required: true, message: '', trigger: 'change' }"
          >
            <el-select v-model="editform.type" placeholder="">
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="name"
            :rules="[{ required: true, message: '', trigger: 'blur' }]"
          >
            <el-input v-model="editform.name" autocomplete="off" placeholder=''></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input type='textarea' v-model="editform.description" autocomplete="off"  placeholder=""></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="details"
            :rules="[{ required: true, message: '', trigger: 'blur' }]"
          >
            <el-input type='textarea' v-model="editform.details" autocomplete="off"  placeholder=""></el-input>
          </el-form-item>
          <!-- <el-form-item
            label=""
            :label-width="formLabelWidth"
            :rules="[{ required: true}]"
            v-for="(item, index) in editform.projectGroups" :key="index"
          >
            <el-col :span="11">
                <el-form-item>
                    <el-input v-model="item.name" autocomplete="off" class="groupNameInput" placeholder=''></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="10">
                <el-form-item >
                    <label>:</label><el-input v-model="item.sampleSize" autocomplete="off" class="groupCountInput" type='number' placeholder=''></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                <i class="el-icon-circle-plus-outline"></i>
                <i class="el-icon-remove-outline"></i>
            </el-col>
          </el-form-item> -->
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="submitUpdate('editform')"> </el-button>
        </div>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="QrcodeDialog"
        class="addPlatform"
