<template>
  <!--  -->
  <div>
    <div class="baseinfo">
      <div class="editBtn">
        <el-button type="text" icon="el-icon-edit-outline" @click="editBaseinfoBtn"></el-button>
      </div>
      <el-form label-position="top" label-width="80px" inline>
        <el-form-item label=":" style="width:450px;margin-right:70px;">
          <el-input v-model="baseinfo.type" readonly></el-input>
        </el-form-item>
        <el-form-item label=":" style="width:450px;">
          <el-input v-model="baseinfo.name" readonly></el-input>
        </el-form-item>
        <el-form-item label=":" style="width:975px;margin-right:8px;">
          <el-input type="textarea" v-model="baseinfo.description" readonly></el-input>
        </el-form-item>
        <el-form-item label=":" style="width:975px;margin-top:8px;">
          <el-input type="textarea" v-model="baseinfo.details" readonly></el-input>
        </el-form-item>
      </el-form>
      <div class="title">
        <span></span>
        <el-button class="addBtn" @click="createGroupDialog = true;handleGetList({projectId : projectInfo.id});"></el-button>
      </div>
      <div class="table" style="margin-top:20px;">
        <!-- width:975px; -->
        <el-table
          :data="tableData"
          style="width: 100%"
          border
          :header-cell-style="{background:'#F2F2F2',fontWeight:'normal'}"
        >
          <el-table-column prop="id" label="ID" width="100" align="center"></el-table-column>
          <el-table-column prop="name" label="" width="170" align="center"></el-table-column>
          <el-table-column prop="sampleSize" label="" width="150" align="center"></el-table-column>
          <el-table-column prop="recruitedPatients" label="" width="200" align="center"></el-table-column>
          <el-table-column prop="questionnairePlan.name" label="" width="200" align="center"></el-table-column>
          <el-table-column label="" width="155" align="center">
            <template slot-scope="scope" class="btns">
              <el-button icon="el-icon-edit" @click.stop="edit(scope.row)" size="mini"></el-button>
              <el-button icon="el-icon-delete" size="mini" @click.stop="delRow(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editBaseInfoDialog"
      class="addPlatform"
      width="760px"
      :close-on-click-modal="false"
    >
      <el-form :model="editBaseinfo" ref="editBaseInfo">
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="name"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input v-model="editBaseinfo.name" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="type"
          :rules="{ required: true, message: '', trigger: 'change' }"
        >
          <el-select v-model="editBaseinfo.type" placeholder="">
              <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-input
            type="textarea"
            v-model="editBaseinfo.description"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="details"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input
            type="textarea"
            v-model="editBaseinfo.details"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitBaseInfo('editBaseInfo')"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editBaseInfoTableDialog"
      class="addPlatform"
      width="760px"
      :close-on-click-modal="false"
    >
      <el-form :model="editBaseInfoTable" ref="editBaseInfoTable">
        <el-form-item
          label=""
          label-width="100px"
          prop="name"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input v-model="editBaseInfoTable.name" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item
          label=""
          label-width="100px"
          prop="sampleSize"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input
            v-model="editBaseInfoTable.sampleSize"
            autocomplete="off"
            placeholder=""
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px" prop="use">
          <el-select v-model="editBaseInfoTable.questionnairePlanId" placeholder="">
            <el-option v-for="(item, index) in surveyPlan" :key="index" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitBaseInfoTable('editBaseInfoTable')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="createGroupDialog"
      class="addPlatform"
      width="760px"
      :close-on-click-modal="false"
      destroy-on-close
    >
      <el-form :model="createGroup" ref="createGroup">
        <el-form-item
          label=""
          label-width="100px"
          prop="name"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input v-model="createGroup.name" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item
          label=""
          label-width="100px"
          prop="sampleSize"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input
            v-model="createGroup.sampleSize"
            autocomplete="off"
            placeholder=""
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px" prop="questionnairePlanId">
          <el-select v-model="createGroup.questionnairePlanId" placeholder="">
            <el-option v-for="(item, index) in surveyPlan" :key="index" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitCreateGroup('createGroup')"> </el-button>
      </div>
    </el-dialog>
  </div>
